/* global PUBLIC_PATH */
import { h, Component } from 'preact';
import { Router } from 'preact-router';

import Header from './header';
import Home from '../routes/home';

const Stats = ({ title, value }) => (
	<span>
		<span class="title">{title}: </span>
		{value}
		&nbsp;
	</span>
);

const Footer = ({ fps, ms, stats }) => (
	<footer class="footer">
		<Stats title="fps" value={Math.round(fps)} />
		<Stats title="ms" value={ms.toFixed(1)} />
		{(stats || []).map(({ title, value }) => (
			<Stats title={title} value={value} />
		))}
	</footer>
);

const getTime = () => (performance || Date).now();

// <Stat title="ms" value={`${ms.toFixed(1)} (${(ms * 6) | 0}%)`} />

export default class App extends Component {
	state = {
		loop: null,
		fps: 0,
		ms: 0,
		stats: null
	};

	handleRoute = e => {
		this.currentUrl = e.url;
		this.setState({
			loop: null,
			fps: 0,
			ms: 0,
			stats: null
		});
	};

	begin() {
		this.beginTime = getTime();
	}

	end() {
		this.frames++;

		const now = getTime();
		const passed = now - this.prevTime;

		if (passed >= 1000) {
			const ms = now - this.beginTime;
			const fps = (this.frames * 1000) / passed;
			this.setState({ fps, ms });

			this.prevTime = now;
			this.frames = 0;
		}

		/*
		if (now - this.prevTime >= 1000) {
			const ms = now - this.beginTime;
			const fps = (this.frames * 1000) / (now - this.prevTime);
			this.setState({ fps, ms });

			this.prevTime = now;
			this.frames = 0;
		}
		*/
	}

	start = loop => {
		this.setState({ loop });
		this.loop();
	};

	loop = () => {
		if (this.state.loop) {
			this.begin();
			const stats = this.state.loop();
			this.setState({ stats });
			this.end();
			requestAnimationFrame(this.loop);
		}
	};

	componentDidMount() {
		this.beginTime = getTime();
		this.prevTime = this.beginTime;
		this.frames = 0;
	}

	/*
					<Home path={PUBLIC_PATH} start={this.start} />
	    				<Profile path="/profile/" user="me" />
    				<Profile path="/profile/:user" />
*/

	render() {
		const { fps, ms, stats, loop } = this.state;
		return (
			<div id="app">
				<Header />
				<Router onChange={this.handleRoute}>
					<Home path={PUBLIC_PATH} start={this.start} />
				</Router>
				{loop && <Footer fps={fps} ms={ms} stats={stats} />}
			</div>
		);
	}
}



